//   : https://openweathermap.org/weather-conditions#Weather-Condition-Codes-2
import { 
  //  
  Sun,               //  ()
  MoonStar,          //  ()
  
  // 
  Cloud,             //  ()
  CloudSun,          //   ()
  CloudMoon,         //   ()
  Cloudy,            //  
  
  //  
  CloudSunRain,      //  ()
  CloudMoonRain,     //  ()
  CloudDrizzle,      // 
  Snowflake,         // 
  
  //  
  Zap,               // 
  Tornado,           // 
  
  //  
  CloudFog,          // 
  Haze,              // 
  Wind,              // //
  AlertTriangle      //  
} from 'lucide-react';

// OpenWeatherMap weatherId    
export const getWeatherIcon = (weatherId, timestamp = null) => {
  // /  (timestamp  ,    )
  const checkTime = timestamp ? new Date(timestamp) : new Date();
  const hour = checkTime.getHours();
  const isNight = hour < 6 || hour > 18;
  
  //  ID   
  if (weatherId >= 200 && weatherId < 300) {
    //  (Thunderstorm)
    return Zap;

  } else if (weatherId >= 300 && weatherId < 400) {
    //  (Drizzle)

    return CloudDrizzle;

  } else if (weatherId >= 500 && weatherId < 600) {
    //  (Rain) -    
    if (weatherId === 511) {
      // Freezing rain ( )
      return Snowflake;
    } else if (isNight) {
      return CloudMoonRain; //   ()
    } else {
      return CloudSunRain;  //   ()
    }
 
  } else if (weatherId >= 600 && weatherId < 700) {
    //  (Snow)
    return Snowflake;

  } else if (weatherId >= 700 && weatherId < 800) {
    //   (Atmosphere) -    
    switch (weatherId) {
      case 701: // Mist () - mist
        return CloudFog;

      case 711: // Smoke () - smoke
        return CloudFog;
        
      case 721: // Haze () - haze
        return Haze;

      case 731: // Dust () - sand/dust whirls
        return Wind;

      case 741: // Fog () - fog
        return CloudFog;

      case 751: // Sand () - sand
        return Wind;

      case 761: // Dust () - dust
        return Wind;

      case 762: // Ash () - volcanic ash
        return AlertTriangle;

      case 771: // Squalls () - squalls
        return Wind;

      case 781: // Tornado () - tornado
        return Tornado;

      default:
        return Wind;
    }
  } else if (weatherId === 800) {
    //  (Clear sky)
    return isNight ? MoonStar : Sun;

  } else if (weatherId > 800) {
    //  (Clouds) -  /  

    if (weatherId === 801) {
      // Few clouds (11-25%) - /    
      return isNight ? CloudMoon : CloudSun;

    } else if (weatherId === 802) {
      // Scattered clouds (25-50%) -  

      return Cloud;
    } else {
      // Broken/Overcast clouds (51-100%) -  
      return Cloudy;
    }
  }
  // 
  return Cloud;
};

//   (description  )
export const WeatherDescriptionWithIcon = ({ weather, className = "" }) => {
  if (!weather) {
    return <p className={className}>Loading...</p>;
  }

  const IconComponent = getWeatherIcon(weather.weatherId, weather.timestamp);
  const description = weather.description 
    ? weather.description.charAt(0).toUpperCase() + weather.description.slice(1).toLowerCase()
    : 'Loading...';

  return (
    <div className={`weather-description-container ${className}`}>
      <IconComponent 
        size={20} 
        className="weather-icon"
        color="#FFFFFF"
      />
      <span className="weather-text">{description}</span>
    </div>
  );
};